.wrapper-section1{
    border: 1px solid black;
}
.text-section{
    display: flex;
    margin: auto;
    flex-direction: column;
    align-items: center;
    max-width: 90%;
}
.text-section h1{
    color: rgb(0,0,0);
    font-size: 50px;
}
.text-section p{
    margin-top: 30px;
    margin-bottom: 30px;
}
img{
    max-width: 100%;
    max-height: 400px;
    margin: auto;
}
.button-section1{
    border: 1px solid black;
    padding-left:15px;
    padding-right:15px;
    padding-top: 5px;
    padding-bottom: 5px;
    cursor: pointer;
    border-radius: 15px;
}